input{
    display: flex;
    flex-direction: column;
   margin: 20px auto;
    width: 400px;
    height: 35px;
    border: none;
    border-bottom: 1px solid;
    background-color: rgba(255, 255, 255, 0);
    color: white;
}
input::placeholder{
    color: white;
}
.error-message{
    text-align: center;
    color: red;
    font-size: 12px;
}
.loginbtn {
    width: 150px;
    height: 25px;
    text-align: center;
    margin: 30px auto;
    border-radius: 10px;
    background-color: rgb(147, 153, 248);
    border: none;
    font-size: 18px;
    box-shadow:0px 0px 50px 0px rgb(122, 130, 243) ;
    font-weight: bold;
    color: white;
  }